<script>
import DatePicker from 'v-calendar/lib/components/date-picker.umd'
import MyPagination from "@/components/MyPagination.vue";

export default {
  name: "NotificationsNew",
  components: {
    MyPagination,
    DatePicker
  },
  data() {
    return {
      checkedSmsSettings : false,
      showSettingsLang : false,
      inputLangValue : 'Русский',
      checkedEmailSettings : false,
      checkedSystemSettings : false,
      showNotificationsNew : true,
      showNotificationsRead : false,
      showNotificationsSettings : false,
      value: '',
      context: null,
      showCalendar: false,
      showClock: false,
      // currentPage : 3,
      // rows : 5,
      // perPage : 1,
      date: new Date(),
      currentPage: 1,
      selectedHour: '00',
      selectedMinute: '00',
      attributes: [
        {
          key: 'today',
          highlight: {
            style: {
              background: '#C460C0',
              color: '#FFFFFF',
            },
            contentStyle: {
              color: '#FFFFFF',
            },
          },
          dates: new Date(),
        },
      ],
    }
  },
  methods: {

    newLang(lang) {
      this.inputLangValue = lang
      this.showSettingsLang = false
    },

    closeLangList() {
      setTimeout(() => {
        this.showSettingsLang = false
      }, 300)
    },

    showActiveTab(tab) {
      if (tab === 'new') {
        this.showNotificationsNew = true
        this.showNotificationsRead = false
        this.showNotificationsSettings = false

      } else if (tab === 'read') {
        this.showNotificationsNew = false
        this.showNotificationsRead = true
        this.showNotificationsSettings = false

      } else if (tab === 'settings') {
        this.showNotificationsNew = false
        this.showNotificationsRead = false
        this.showNotificationsSettings = true
      }
    },

    onContext(ctx) {
      this.context = ctx
    },
    onPageChange(page) {
      this.currentPage = page;
    },
    showClockFunc() {
      this.showClock = !this.showClock
      this.showCalendar = false
    },
    showCalendarFunc() {
      this.showCalendar = !this.showCalendar
      this.showClock = false
    }
  },
  computed: {
    hours() {
      const rangeHours = [];

      for (
          let i = 0;
          i <= 23;
          i++
      ) {
        i < 10 ? rangeHours.push("0" + i) : rangeHours.push("" + i)
      }

      return rangeHours;
    },
    minutes() {
      const rangeMinutes = [];

      for (
          let i = 0;
          i <= 59;
          i++
      ) {
        i < 10 ? rangeMinutes.push("0" + i) : rangeMinutes.push("" + i)
      }

      return rangeMinutes;
    }
  }
}
</script>

